// Пресеты цветов
$black: rgb(50, 50, 50);

// Граница изменения направления слайдов
$v-direction-limit: 1023px;
$h-direction-limit: 1024px;

// TODO Временный тестовый миксин
@mixin temp {
  width: 100vw;
  min-width: 320px;

  height: 100vh;
  min-height: 640px;

  padding: 2em;

  flex-shrink: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h1 {
    font-family: Roboto, 'sans-serif';
    font-size: 2em;
    text-align: center;
  }

  p {
    font-family: Roboto, 'sans-serif';
    font-size: 1.2em;
    text-align: center;
  }
}

// TODO Временные стили для границ слайдов
.bordered + .bordered {
  border-top: 1px solid $black;
  border-left: none;
}

@media (min-width: $h-direction-limit) {
  .bordered + .bordered {
    border-top: none;
    border-left: 1px solid $black;
  }
}



